import initFun from "./comfun/index"
import ininRun from "./initRun"
import initState from "./inteState"

export function initMixin(QUI){
    QUI.prototype._init = function(options){
        let vm = this
        vm.$options = options
        // 合并方法
        initFun(vm)

        // 初始化状态
        initState(vm)

        if(vm.$options.el){
            vm.$mount(vm.$options.el)
        }
        // 初始化QUI入口
        ininRun(vm)
    }
    QUI.prototype.$mount = function(el){
        // 模板编译优先级 el < template < render
        el = document.querySelector(el)
        let vm = this
        let options = vm.$options
        if(!options.render){
            let template =  options.template
            if(!template && el){
                el = el.outerHTML
                // console.log(el)
            }
        }
    }
}

