// 原始数组原型链
let OrginArrayMethod = Array.prototype

// 继承原型上的方法
let ArrayMethod = Object.create(OrginArrayMethod)

let methods = [
    "push",
    "pop",
    "unshift",
    "shift",
    "splice"
]

methods.forEach(item=>{
    ArrayMethod[item] = function(...args){
        // 数组方法劫持
        let res =  OrginArrayMethod[item].call(this,args)

        // 数组新增属性劫持
        let addValue
        switch (item) {
            case "push":
            case "unshift":  
                addValue = args
                break;
            case "splice":  
                addValue = splice(2)
                break;
            default:
                break;
        }
        let ob = this.__ob__
        if(addValue){
            ob.walkArray(addValue)
        }
        return res
    }
})

export default  ArrayMethod
