export default function ininRun(vm){
    vm.setRem()
    vm.windowSize()
    vm.getPageSize()
    vm.loadViewer()
    if(vm.QRoptions){
        vm.createQRcode(vm.QRoptions)
    }
    // 视口变化监听
    window.addEventListener("resize", function () {
        __reset()
    })
    function __reset() {
        console.clear()
        vm.setRem()
    }
}